@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
*{
  margin: 0;
  padding: 0;
  border: none;
}

body{
  font-family: 'Roboto', sans-serif;
}
.App{
  width: 100%;
  height: 100vh;
  padding-top: 18vh;
}

#quote-box{
    width: 40%;
    margin: 0 auto;
    padding: 40px;
    background-color: #fff;
}
.quote{
    text-align: center;
    font-size: 1.5rem;
    line-height: 1.3;
}
.quote i{
    margin-right: 10px;
}
#author{
    display: block;
    margin: 12px 0;
    text-align: right;
}
.buttons{
    margin: 10px 0;
    padding-top: 10px;
    display: flex;
}
.buttons *:hover{
    cursor: pointer;
    opacity: 0.9;
}
.buttons i{
    font-size: 30px;
    margin-right: 10px;
}
#new-quote{
    width: 90px;
    margin-left:auto;
    padding: 8px;
    color: #fff;
    border-radius: 5px;
}
